.cards-container {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
  margin-bottom: 50px;
  align-items: center;
  padding: 80px 20px;
}

/* Section Styling */
.section4,
.section4-1 {
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: 100%; /* Full width, flexible */
  max-width: 1100px; /* Limits width on larger screens */
  height: auto; /* Remove fixed height */
  margin: 0 auto;
  position: relative;
  background: url('../../src/assets/card1.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 30px 30px;
  margin-bottom: 40px;
  overflow: hidden;
}

/* Title and Description */
.section4-title, 
.section4-1-title {
  font-size: 1.4rem;
  margin-bottom: 30px;
  color: #333;
  text-align: left;
  font-family: 'Lato';
  font-weight: bold;
}

.section4-description, 
.section4-1-description {
  font-size: 1.3rem;
  color: #656565;
  text-align: center;
  margin-top: 60px;
  font-family: 'Lato';
  font-weight: bold;
}

/* Carousel */
.product-carousel, 
.product-1-carousel {
  display: flex;
  overflow-x: scroll;  
  scroll-behavior: smooth; 
  padding: 20px;
  gap: 20px; 
  -ms-overflow-style: none;
}

.product-carousel::-webkit-scrollbar, 
.product-1-carousel::-webkit-scrollbar {
  display: none; /* Hides scrollbar */
}

.product-carousel-track,
.product-1-carousel-track {
  display: flex;
  animation: scroll 30s linear infinite; /* Infinite scrolling */
}

/* Animation for Scrolling */
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%); /* Scrolls halfway for duplicated content */
  }
}

.product-item,
.product-1-item {
  padding: 0; 
  background-color: #5791dc;
  text-align: center;
  transition: transform 0.3s;
  margin-right: 20px;
}

.product-item img, 
.product-1-item img {
  max-width: 100%;
  height: auto;
  display: block;
  margin-bottom: 10px;
}

.product-item:hover,
.product-1-item:hover {
  transform: scale(1.05); 
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

/* Media Query for smaller screens */
@media (max-width: 768px) {
  .section4,
  .section4-1 {
    width: 100%; /* Ensure full width on smaller screens */
    max-width: 100%; /* No width limitation */
    padding: 20px; /* Reduce padding for mobile */
    margin-bottom: 20px;
  }

  .section4-title, 
  .section4-1-title {
    font-size: 1.2rem; /* Adjust font size for smaller screens */
  }

  .section4-description, 
  .section4-1-description {
    font-size: 1rem; /* Adjust description size */
  }

  .product-item,
  .product-1-item {
    width: 120px; /* Smaller item width for mobile */
  }

  /* Reduce carousel padding for mobile */
  .product-carousel, 
  .product-1-carousel {
    padding: 10px;
  }
}

/* Additional for very small screens like iPhone XR */
@media (max-width: 480px) {
  .section4,
  .section4-1 {
    padding: 15px; /* Further reduce padding */
  }

  .section4-title, 
  .section4-1-title {
    font-size: 1rem; /* Smaller font size */
  }

  .section4-description, 
  .section4-1-description {
    font-size: 0.9rem;
  }

  .product-item,
  .product-1-item {
    width: 100px; /* Further reduced width for very small screens */
  }
}
