.navbar-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 25px;
    background:rgb(63, 138, 144);
    position:sticky;
    z-index: 999;
    top:0;
   
}

.navbar.active{
    background: rgb(63, 138, 144);
}



.navbar .logo {
    color: white;
    width: 60px;
    margin-left: 20px; 
}


.navbar ul {
    list-style: none;
    display: flex;
}

.navbar ul li {
    margin-left: 20px;
}


.navbar ul li a{
    text-decoration: none;
    color: #ffffff;
    font-size: 18px;
    margin: 0 10px;
    position: relative;
    
}    
.navbar ul li a:hover{
    color:#ffffff;
 
}
.navbar ul li a::before{
        content: '';
        position: absolute;
        bottom: -4px;
        left: 0;
        width: 0%;
        height: 3px;
        background-color: #ee0653;
        transition: all 0.5s;

}

.navbar ul li a:hover::before {

    width:100%;
    
}



   
      
    @media (max-width: 768px) {
        .navbar-links {
          position: fixed;
          top:0;
          right:0;
          height: 100vh;
          width: 100vw;
          background-color: rgba(23, 27, 80, 0.9); /* Dark background with transparency */
          flex-direction: column;
          justify-content: center;
          z-index: 999;
          transform: translateX(-100%); /* Initially hidden */
          transition: transform 0.3s ease-in-out;
        }
        
        .navbar-links.active {
          transform: translatez(0); /* Slide in the menu */
        }
       
        .navbar-links  li {
          padding:10px 0px;
          margin: 20px 0;
        }
      
        .navbar-links li a {
          font-size: 1.8rem;
          color: #16678f;
          text-decoration: none;
          padding: 20px;
          transition: color 0.3s ease;
        }
      
        .navbar-links ul li a:hover {
          color: #5c81f9; /* Change color on hover */
        }
      
        .navbar-hamburger {
          cursor: pointer;
          color: #fff;
          font-size: 2rem;
          z-index: 1001;
          position: relative;
        }
      }
