
.cards-container-2{
  display: flex; 
  justify-content: space-between; 
  gap: 20px;
  padding: 50px;
  flex-direction: row; 
  background-color: #ffffff;
  margin-top: -90px;
}

.section6, .section6-1 {
  flex: 0 0 48%; 
  border-radius: 10px; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
  padding: 20px;
  width: 400px;
  height: 600px;
  text-align: left;
  position: relative;
  background: url('../../src/assets/card2.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: box-shadow 0.3s ease;

}
.section6-img {
  width: 90%; /* Adjust width to match the card */
  height: 300px;
  display: block;  
  margin:0 auto 10px;
  margin-bottom: 10px;
  text-align: center;
}
.section6-1-img {
  width: 55%; /* Adjust width to match the card */
  height: auto;
  display: block;  /* Ensure the image is treated as a block element */
  margin: 0 auto 5px;
}
.arrow, .arrow-1 {
  position: absolute; 
  top: 30px;
  right: 20px;
  transform: translateY(-50%); 
  font-size: 1.5rem;
  color: #000000;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.arrow:hover, .arrow-1:hover {
  transform: translateY(-50%) translateX(5px); 
}

.section6:hover, .section6-1:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
}

.section6-title, .section6-1-title {
  font-size: 1.1rem;
  margin-bottom: 40px;
  color: #333;
  text-align: left;
}

.section6-1-description {
  font-size: 1.2rem;
  color: #5b5b5b;
  text-align: left;
  margin-top: 10px;
}
.section6-description {
  font-size: 1.2rem;
  color: #5b5b5b;
  text-align: left;
  margin-top: 32px;
}

.product-item, .product-1-item {
  flex: 0 0 auto;  
  width: 150px;  
  background-color: #ffffff;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
}
.button, .button-1 {
  display: inline-block;
  padding: 10px 60px; 
  font-size: 1rem; 
  color: #ffffff; 
  background-color: #5c81f9; 
  border-radius: 12px; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
  text-decoration: none; 
  transition: background-color 0.3s ease, box-shadow 0.3s ease; 
  margin-top: 40px;
  left: -5px; 
  position: relative;
}

.button:hover, .button-1:hover {
  background-color: rgb(72, 95, 170); 
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); 
}

/* Media Query for Tablets and Smaller Screens */
@media (max-width: 768px) {
  .cards-container-2 {
    flex-direction: column; /* Stack cards vertically */
    padding: 30px 30px; /* Adjust padding for smaller screens */
    margin-top: -120px;
  }

  .section6, .section6-1 {
    width: 100%; /* Ensure full width on smaller screens */
    height: auto; /* Adjust height to fit content */
    margin-bottom: 30px; /* Add margin between stacked cards */
  }

  .product-item, .product-1-item {
    width: 120px;
  }
  .arrow-1 {
    position: absolute; 
    top: 30px;
    right: 10px;
    transform: translateY(-50%); 
    font-size: 1.5rem;
    color: #000000;
    cursor: pointer;
    transition: transform 0.3s ease;
  }
  .section6-img {
    width: 85%; /* Adjust width to match the card */
    height: auto;
    display: block;  /* Ensure the image is treated as a block element */
    margin: 0 auto 5px;
  }
}


@media (max-width: 480px) {
  .section6-title, .section6-1-title {
    font-size: 1rem; /* Adjust font size for small screens */
  }

  .section6-description, .section6-1-description {
    font-size: 1rem; /* Smaller text for better readability on mobile */
  }

  .button, .button-1 {
    padding: 10px 30px; /* Reduce padding on buttons */
    font-size: 0.9rem; /* Smaller button text */
  }
}
