.section-wrapper {
  text-align: center;
  background-color: #ffffff;
  padding: 80px 10px;
}

.section-title {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 20px;
}

.section-description {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 60px;
  margin-left: 30px;
  margin-right: 30px;
}

.section-header-img {
  width: 50%; 
  height: auto;
  display: block;
  margin: 0 auto 40px; 
}
.cards-container-1{
  display: flex; 
  justify-content: space-between; 
  gap: 20px;
  padding: 50px;
  margin-bottom: 20px;
  flex-direction: row; 
}

.section5, .section5-1 {
  flex: 0 0 48%; 
  border-radius: 10px; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
  padding: 20px;
  width: 400px;
  height: 600px;
  text-align: left;
  position: relative;
  background: url('../../src/assets/card2.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: box-shadow 0.3s ease;
}
.section5-img{
  width: 60%; 
  height: 200px;
  display: block; 
  margin: 0 auto 90px;
}
.section5-1-img {
  width: 45%; 
  height: 268px;
  display: block;  
  margin: 0 auto 10px;
}

.arrow, .arrow-1 {
  position: absolute; 
  top: 29px;
  right: 20px;
  transform: translateY(-50%); 
  font-size: 1.5rem;
  color: #000000;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.arrow:hover, .arrow-1:hover {
  transform: translateY(-50%) translateX(5px); 
}

.section5:hover, .section5-1:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
}

.section5-title, .section5-1-title {
  font-size: 1.1rem;
  margin-bottom: 40px;
  color: #333;
  text-align: left;
}

.section5-description, .section5-1-description {
  font-size: 1.2rem;
  color: #5b5b5b;
  text-align: left;
  margin-top: 10px;
}

.button, .button-1 {
  display: inline-block;
  padding: 10px 60px; 
  font-size: 1rem; 
  color: #ffffff; 
  background-color: #5c81f9; 
  border-radius: 12px; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
  text-decoration: none; 
  transition: background-color 0.3s ease, box-shadow 0.3s ease; 
  margin-top: 40px;
  left: -5px; 
  position: relative;
}

.button:hover, .button-1:hover {
  background-color: rgb(72, 95, 170); 
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); 
}

@media (max-width: 768px) {
  .cards-container-1 {
    flex-direction: column; /* Stack cards vertically on smaller screens */
    padding: 30px 20px; /* Adjust padding for smaller screens */
  }

  .section5, .section5-1 {
    width: 100%; /* Ensure cards take full width on mobile */
    height: auto; /* Auto height for flexibility */
    margin-bottom: 20px; /* Add space between stacked cards */
  }

  .section5-title, .section5-1-title {
    font-size: 1rem; /* Adjust font size for smaller screens */
  }

  .section5-description, .section5-1-description {
    font-size: 1rem; /* Smaller text for readability on mobile */
  }

  .button, .button-1 {
    padding: 10px 40px; /* Reduce button size on mobile */
    font-size: 0.9rem; /* Smaller button text */
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 2rem; /* Smaller title for very small screens */
  }

  .section-description {
    font-size: 1rem; /* Smaller description text */
    margin-left: 15px; 
    margin-right: 15px;
  }

  .section5-img, .section5-1-img {
    width: 80%; /* Adjust image width for mobile */
    margin-bottom: 20px;
  }
}