.section8-wrapper {
  background-color: #000000;
  padding: 70px;
}

.section8-content p {
  text-align: center;
  max-width: 1000px;
  margin: 0 auto;
  font-size: 1.8rem;
  color: #ffffff;
  margin-bottom: 60px;
  font-family: "Tahoma";
}

.section8-row {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  text-align: left;
  font-family: "Tahoma";
}

.section8-item {
  flex: 1;
  padding: 10px;
  font-family: "Tahoma";
}

.section8-item h1 {
  font-size: 1.6rem;
  color: #ffffff;
  margin-bottom: 30px;
}

.section8-item p {
  font-size: 1.2rem;
  color: #f4f4f4;
  margin-bottom: 40px;
  margin-top: 10px;
  line-height: 2rem;
}

/* Responsive Layout for Tablets and Mobile */
@media (max-width: 768px) {
  .section8-row {
    flex-direction: column; /* Stack items vertically on smaller screens */
  }

  .section8-item {
    padding: 20px 0; /* Add more padding for better spacing on mobile */
  }

  .section8-item h1 {
    font-size: 1.4rem; /* Slightly smaller font size for headings */
  }

  .section8-item p {
    font-size: 1rem;
    line-height: 1.8rem; /* Adjust line height for readability on smaller screens */
  }
}

/* Further adjustments for very small screens like iPhone XR */
@media (max-width: 480px) {
  .section8-wrapper {
    padding: 40px 20px; /* Reduce padding for very small screens */
  }

  .section8-content p {
    font-size: 1.4rem; /* Smaller font for the main paragraph */
    margin-bottom: 40px;
  }

  .section8-item h1 {
    font-size: 1.2rem;
  }

  .section8-item p {
    font-size: 0.9rem;
    line-height: 1.6rem;
    margin-bottom: 30px;
  }
}
