.section7 {
  padding: 40px;
  text-align: center;
  background-color: #ebebeb;
}

.section7-title {
  font-size: 2rem;
  color: #000000;
  margin-bottom: 10px;
  margin-top: 10px;
}

.image img {
  width: 100%; /* Ensure image is responsive */
  max-width: 400px; /* Limit the max width on larger screens */
  height: auto;
  margin-bottom: 75px;
}

/* Responsive Layout for Tablets and Mobile */
@media (max-width: 768px) {
  .section7 {
    padding: 30px 20px; /* Reduce padding on smaller screens */
  }

  .section7-title {
    font-size: 1.8rem; /* Slightly smaller font for title */
  }

  .image img {
    max-width: 300px; /* Smaller max width for tablets */
  }
}

@media (max-width: 480px) {
  .section7 {
    padding: 20px 10px; /* Further reduce padding on very small screens */
  }

  .section7-title {
    font-size: 1.5rem; /* Smaller font for the title on mobile */
  }

  .image img {
    max-width: 250px; /* Reduce image size further on mobile */
  }
}
