.section1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 100px 30px; /* Reduced padding for better responsiveness */
    background: url('../../src/assets/image3.jpeg'); 
    background-size: cover;  
    background-position: center; 
    background-repeat: no-repeat; 
    flex-wrap: wrap;
    position: relative;
}

.section1-text {
    flex: 2;
    max-width: 500px;
    font-family: 'Poppins', sans-serif;
    color: rgb(72, 72, 72); 
    font-weight: bold;
}

.section1-text h2 {
    font-size: 2rem; /* Adjusted font size */
    margin-bottom: 40px;
    color: rgb(0, 0, 0);
    font-weight: bold;
    text-align: center;
}

.section1-text p {
    font-size: 16px; /* Adjusted for better readability on mobile */
    line-height: 1.5;
    text-align: justify;
}

.section1-image {
    flex: 10%;
    display: flex;
    justify-content: center;
}

.section1-image img {
    width: 100%; /* Ensures image scales well on mobile */
    height: auto;
    object-fit: cover;
}

.left-image {
    margin-right: 20px;
}

.right-image {
    margin-left: 20px;
}

/* Media Query for smaller screens */
@media (max-width: 768px) {
    .section1 {
        flex-direction: column; /* Stacks content vertically */
        padding: 50px 20px; /* Reduced padding for mobile */
    }

    .section1-text {
        margin-bottom: 30px;
        text-align: center; /* Center text on mobile for better visual flow */
    }

    .section1-text h2 {
        font-size: 1.8rem; /* Slightly smaller heading for mobile */
    }

    .section1-text p {
        font-size: 14px; /* Reduce text size for mobile */
        line-height: 1.4;
    }

    .section1-image {
        margin-bottom: 20px;
    }

    .section1-image img {
        max-width: 80%; /* Ensures image doesn't overflow the screen */
    }
}
@media (max-width: 768px) {
    .section1 {
        flex-direction: column; /* Stacks content vertically */
        padding: 40px 15px; /* Reduced padding further */
    }

    .section1-text {
        margin-bottom: 20px; /* Reduced margin for smaller screens */
        text-align: center; /* Center text on mobile for better flow */
    }

    .section1-text h2 {
        font-size: 1.6rem; /* Slightly smaller heading for mobile */
    }

    .section1-text p {
        font-size: 13px; /* Reduce text size for mobile */
        line-height: 1.3;
    }

    .section1-image img {
        max-width: 90%; /* Ensure image stays within screen bounds */
    }
}

/* Media Query for very small screens (max-width: 480px) */
@media (max-width: 480px) {
    .section1 {
        padding: 30px 10px; /* Further reduce padding for small devices */
    }

    .section1-text h2 {
        font-size: 1.4rem; /* Smaller heading for very small screens */
    }

    .section1-text p {
        font-size: 12px; /* Smaller text for better fit */
        line-height: 1.2;
    }

    .section1-image img {
        max-width: 100%; /* Ensure image stays within screen bounds */
    }
}
